@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
:root {
  --accent-color: #333333;
  --main-bg: #000000;
  --main-text-color: #F2F2F2;
  --main-info-color: #b6bfb7;
  --main-success-color: #4CAF50;
  --main-danger-color: #E53935;
  --secondary-bg: #444444;
  --secondary-color: #EEEEEE;
  --selection-bg: #333333;
  --selection-color: #F1F1F1;
  --monochrome: #FFF;
  --default-transparent: transparent;
  --default-transparent-02: rgb(0 0 0 / 20%);
  --default-transparent-04: rgb(0 0 0 / 40%);
  --default-transparent-06: rgb(0 0 0 / 60%);
  --default-transparent-08: rgb(0 0 0 / 80%);
  --white-transparent-04: rgb(255 255 255 / 40%);
}
* {
  outline: none; 
  box-sizing: border-box;
  text-decoration: none;
  margin: 0;
  padding: 0;
}
::selection {
  color: #F1F1F1;
  color: var(--selection-color);
  background: #333333;
  background: var(--selection-bg);
  text-shadow: none;
}
body {
  overflow: hidden;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: 1.4;
  margin: 0 auto;
  color: #F2F2F2;
  color: var(--main-text-color);
  background: #000000;
  background: var(--main-bg);
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: var(--default-transparent);
  -webkit-app-region: no-drag;
}
#root {
  height: 100vh;
}

.none {
  display: none;
}

.loader {
  display: flex;
}

.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 12px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  min-width: 100px;
  height: 36px;
  border-radius: 6px;
  margin: 8px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn.main {
  background-color: var(--secondary-bg);
}

.btn::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  transition: background-color .1s;
}
.btn:hover::before {
  background-color: var(--default-transparent-02);
}

.file_input {
  display: flex;
  align-items: center;
  padding: 0 10px 0 2px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  min-width: 200px;
  max-width: 320px;
  height: 36px;
  border: 2px solid var(--secondary-bg);
  border-radius: 6px;
  margin: 8px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.file_input .btn {
  height: 28px;
  margin: 0 8px 0 0;
  border-radius: 3px;
  font-size: 15px;
}

.file_input span {
  color: var(--secondary-color);
  font-size: 13px;
  max-width: 180px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.radio_group {
  margin: 8px;
  display: flex;
  align-items: center;
}

.radio_item {
  margin-right: 16px;
}
.radio_item label {
  display: flex;
  align-items: center;
  position: relative;
  color: var(--secondary-color);
  padding-left: 28px;
  cursor: pointer;
}
.radio_item label:before, .radio_item label:after {
  content: '';
  position: absolute;
  border-radius: 50px;
  transition: all .3s ease;
  transition-property: transform, border-color;
}
.radio_item label:before {
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  border: 2px solid var(--white-transparent-04);
}
.radio_item label:after {
  top: 5px;
  left: 5px;
  width: 8px;
  height: 8px;
  transform: scale(0);
  background: var(--monochrome);
}
.radio_item input[type="radio"] {
  display: none;
}
.radio_item input[type="radio"]:checked + label {
  color: var(--main-text-color);
}
.radio_item input[type="radio"]:checked + label:before {
  border-color: var(--monochrome);
  animation: ripple .3s linear forwards;
}
.radio_item input[type="radio"]:checked + label:after {
  transform: scale(1);
}
@keyframes ripple {
  0% {
    box-shadow: 0px 0px 0px 1px rgba(255, 255, 255, 0);
  }
  50% {
    box-shadow: 0px 0px 0px 15px rgba(255, 255, 255, .1);
  }
  100% {
    box-shadow: 0px 0px 0px 15px rgba(255, 255, 255, 0);
  }
}


